body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  color: white;
}

.App-link {
  color: #61dafb;
}
.card {
  border: 1px solid gold;
  color: #fff;
  text-align: center;
  cursor: pointer;
}
.search-wrapper {
  display: inline-flex;
  margin: 0 10px 10px 10px;
}

input {
  outline: none;
  font-size: 15px;
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
}

.search-button {
  font-size: 13px;
  outline: none;
  padding-left: 10px;
  padding-right: 10px;
  border-bottom-left-radius: 1px;
  border-top-left-radius: 1px;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
}
.card-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}

.venues {
  margin: 50px;
  width: 100%;
}

.spinner {
  text-align: center;
}

.venues-wrapper {
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse;
  margin: 10px;
}

.spinner-wrapper {
  display: flex;
  justify-content: center;
}

.search-header {
  margin: 0 0 10px 10px;
  font-weight: 500;
}

.search-venues {
  text-align: center;
  color: gold;
}
.target {
  width: 80px;
  height: 150px;
  text-align: center;
  border: 1px dashed gold;
  padding: 5px;
}
.favorite-item {
  color: #fff;
  text-align: center;
  border: 1px solid gold;
}

.favorite-wrapper {
  text-align: center;
}

.favorite-venues {
  color: gold;
}

.delete-icon {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  font-size: 6px;
}

h4 {
  margin-top: 0;
}

.icon {
  cursor: pointer;
}
.nav {
  display: flex;
  list-style: none;
  justify-content: flex-end;
}

.nav li {
  margin: 10px;
}

.nav li a {
  color: #ffff;
  text-decoration: none;
}

.nav li a:hover {
  text-decoration: underline;
}

.container {
  width: 100%;
}
